import request from 'superagent'
class SessionApi {
  static signup (credentials) {
    return request.post('https://localhost:3000/auth/signup')
      .type('form')
      .send(credentials)
      .then((res, err) => {
        if (err) {
          console.log(err)
        }
        console.log(res.status)
        if (res) {
          return res.body
        }
      })
  }
}

export default SessionApi



