import React, { Component} from 'react'
import PropTypes from 'prop-types'

import cookie from 'react-cookies'
import {bindActionCreators} from 'redux'
import {connect} from 'react-redux'
import * as actions from './actions'
import EducationFrame from './components/EducationFrame'
import EducationForm from './components/EducationForm'
import EducationCard from './components/EducationCard'

class Education extends Component {
  /**
   * Class constructor.
   */
  constructor (props, context) {
    super(props, context)

    const storedMessage = cookie.load('successMessage')
    let successMessage = ''
    if (storedMessage) {
      successMessage = storedMessage
      cookie.remove('successMessage')
    }
    this.state = {
      secretData: '',
      errors: {},
      successMessage,
      education :{
        university : '',
        program:'',
        degree:'',
        grade:'',
        awards:'',
        honors:''

      }
    }
    this.addEducation=this.addEducation.bind(this)
    this.editEducation=this.editEducation.bind(this)
    this.removeEducation=this.removeEducation.bind(this)

    this.onChange =this.onChange.bind(this)
    this.onSubmit=this.onSubmit.bind(this)
  }

  addEducation = (event)=>{
    event.preventDefault()
    this.props.actions.openEditForm()
    //actions
  }
  removeEducation = (event)=>{
    event.preventDefault()
    //actions
  }
  editEducation = (event)=>{
    event.preventDefault()
    //actions
  }


  onChange = ()=>{

  }

  onSubmit=()=> {

  }
  /**
   * This method will be executed after initial rendering.

  /**
   * Render the component.
   */
  render () {

const {educations} = this.props
    if(educations.items) {
      return(
        <div>
          <EducationFrame educations = {educations.items} addEducation={this.addEducation}/>
          {educations.isEditing &&
          <EducationForm education={this.state.education} onSubmit={this.onSubmit} onChange={this.onChange}/>}
        </div>
    )
    }
    else {
      return (<div>Educations</div>)
    }
  }
}


Education .contextTypes = {
  router: PropTypes.object.isRequired,

}

Education.propTypes = {
  educations: PropTypes.object.isRequired,
}
function mapStateToProps (state) {
  console.log(state)
  const {educations} = state.profile.myProfile
  return {educations}
}
function mapDispatchToProps (dispatch) {
  return {
    actions: bindActionCreators(actions, dispatch)
  }
}
export default connect(mapStateToProps, mapDispatchToProps)(Education)


