
import * as types from './actionTypes'
import Api from './api'
import Auth from '../../../../Auth/auth'

export const searchFailed = (credentials, json) => {
  return {
    type: types.SEARCH_USER_FAILED,
    credentials,
    err: json.err,
    message: json.message
  }
}
export const searchRequest = (text) => {
  return {
    type: types.SEARCH_USER_REQUEST,
    text
  }
}
export const searchSuccess = (json) => {
  return {
    type: types.SEARCH_USER_SUCCESS,
    users: json.users,
    message: json.message
  }
}

export const invalidateSearch = (text) => {
  return {
    type: types.INVALIDATE_USER_SEARCH,
    text
  }
}
export function searchUser (text) {
  return function (dispatch) {
    dispatch(searchRequest(text))
    return Api.search(text).then(response => {
      if (response.success) {
        dispatch(searchSuccess(response))
      } else {
        dispatch(searchFailed(text, response))
      }
    }).catch(error => {
      dispatch(searchFailed(text, error.response.body))
    })
  }
}



