import * as types from './actionTypes'

export default function universitySearchReducer (state = {}, action) {
  switch (action.type) {
    case types.SEARCH_UNIVERSITY_SUCCESS:
      return {...state,
        isRequesting: false,
        done: true,
        universities: action.universities,
        message: action.message,
        didInvalidate: false
      }
    case types.SEARCH_UNIVERSITY_FAILED:
      return {...state,
        isRequesting: false,
        done: false,
        err: action.err,
        message: action.message,
        didInvalidate: false
      }
    case types.SEARCH_UNIVERSITY_REQUEST:
      return {...state,
        isRequesting: true,
        done: false,
        didInvalidate: false
      }
    case types.INVALIDATE_UNIVERSITY_SEARCH:
      return {...state,
        didInvalidate: true
      }

    default:
      return state
  }
}



