import React, { Component} from 'react'
import PropTypes from 'prop-types'
import cookie from 'react-cookies'
import {bindActionCreators} from 'redux'
import {connect} from 'react-redux'
import * as actions from './action'
import Loader from '../../../../../components/Spinner/Loader'
import SearchBar from '../../../components/SearchBar'
class UniversitySearch extends Component {
  /**
   * Class constructor.
   */
  constructor (props, context) {
    super(props, context)

    // set the initial component state
    this.state = {
      errors: {},
      search: ''

    }

    this.onSubmit = this.onSubmit.bind(this)
    this.onChange = this.onChange.bind(this)
  }

  componentDidUpdate (prevProps) {
    if (this.props.universitySearch !== prevProps.universitySearch) {
      if (this.props.universitySearch.err) {
        this.setState({
          errors: this.props.universitySearch.err
        })
      }
      if (this.props.universitySearch.done) {
        this.context.router.history.replace('/search')
      }
    }
  }

  componentWillReceiveProps (nextProps) {
    if (this.props.universitySearch.done && (nextProps.universitySearch !== this.props.universitySearch)) {
      this.context.router.history.replace('/search')
    }

    /*  if (nextProps.session.user && nextProps.session.logged) {
        this.context.router.history.replace('/dashboard')
      } */
  }

  componentDidMount () {
    if (this.props.universitySearch.didInvalidate) {

    }
    if (this.props.universitySearch.done) {
      // TODO ADD the URI for Search
      this.context.router.history.replace('/search')
    }
  }
  /**
   * Process the form.
   *
   * @param {object} event - the JavaScript event object
   */
  onSubmit (event) {
    // prevent default action. in this case, action is the form submission event
    event.preventDefault()

      // action
    this.props.actions.searchUniversity(this.state.search)
  }
  /**
   * Change the user object.
   *
   * @param {object} event - the JavaScript event object
   */
  onChange (event) {
    return this.setState({search: event.target.value})
  }

  /**
   * Render the component.
   */
  render () {
    if (this.props.universitySearch.didInvalidate || this.props.universitySearch.isFetching) {
      return (<Loader />)
    }
    return (
      <SearchBar
        onSubmit={this.onSubmit}
        onChange={this.onChange}
        errors={this.state.errors}
        search={this.state.search} />

    )
  }
}

UniversitySearch.propTypes = {
  universitySearch: PropTypes.object.isRequired
}
UniversitySearch.contextTypes = {
  router: PropTypes.object.isRequired
}
function mapStateToProps (state) {
  console.log(state)
  const { universitySearch } = state.search.publicContentSearch
  return {
    universitySearch
  }
}

function mapDispatchToProps (dispatch) {
  return {
    actions: bindActionCreators(actions, dispatch)
  }
}
export default connect(mapStateToProps, mapDispatchToProps)(UniversitySearch)



