
import request from 'superagent'
import Auth from '../../../../Auth/auth'
class SessionApi {
  static login (credentials) {
    return request.post('https://localhost:3000/auth/login')
      .type('form')
      .send(credentials)
      .then((res, err) => {
        if (err) {
          console.log(err)
        }
        console.log(res.status)
        if (res) {
          return res.body
        }
      })
  }

  static uploadCV (file) {
    var cv = new FormData()
    cv.append('file', file)
    // const headers = this.requestHeaders()
    return request.post('https://localhost:3000/api/private/myprofile/cv')
      .set({'Authorization': 'Bearer ' + Auth.getToken()})
      .send(cv)
      .on('progress', function (e) {
        console.log('Percentage done: ', e.percent)
      })
      .then((res, err) => {
        if (err) {
          console.log(err)
        }
        console.log(res.status)
        if (res) {
          return res.body
        }
      })
  }

  // TODO NEED TO MAKE IT WORKING
  static download (id) {
    return request.get('https://localhost:3000/api/private/myprofile/cv/' + id)
      .set({'Authorization': 'Bearer ' + Auth.getToken()})

      .then((res, err) => {
        if (err) {
          console.log(err)
        }
        console.log(res.status)

        if (res) {
          // return res.body
          return res.text
        }
      })
  }
  static convertPDF (id) {
    return request.get('https://localhost:3000/api/private/myprofile/cv/' + id + '/xml')
      .set({'Authorization': 'Bearer ' + Auth.getToken()})

      .then((res, err) => {
        if (err) {
          console.log(err)
        }
        console.log(res.status)
        return res.body
        // return res.text
      })
  }
}
export default SessionApi



