import * as types from './actionTypes'

export default function userSearchReducer (state = {}, action) {
  switch (action.type) {
    case types.SEARCH_USER_SUCCESS:
      return {...state,
        isRequesting: false,
        done: true,
        users: action.users,
        message: action.message,
        didInvalidate: false
      }
    case types.SEARCH_USER_FAILED:
      return {...state,
        isRequesting: false,
        done: false,
        err: action.err,
        message: action.message,
        didInvalidate: false
      }
    case types.SEARCH_USER_REQUEST:
      return {...state,
        isRequesting: true,
        done: false,
        didInvalidate: false
      }
    case types.INVALIDATE_USER_SEARCH:
      return {...state,
        didInvalidate: true
      }

    default:
      return state
  }
}



