import request from 'superagent'
import Auth from '../../../../Auth/auth'
class SessionApi {
  static login (credentials) {
    return request.post('https://localhost:3000/auth/login')
      .type('form')
      .send(credentials)
      .then((res, err) => {
        if (err) {
          console.log(err)
        }
        console.log(res.status)
        if (res) {
          return res.body
        }
      })
  }

  static authLogin () {
    return request.get('https://localhost:3000/api/myprofile')
      .set({'Content-Type': 'application/json', 'Authorization': 'Bearer ' + Auth.getToken()})
      .then((res) => {
        return res.body
      })
      .catch(error => {
        return error
      })
  }
}

export default SessionApi



