import React, { Component} from 'react'
import PropTypes from 'prop-types'
import SearchBar from './components/SearchBar'
import cookie from 'react-cookies'
import {bindActionCreators} from 'redux'
import {connect} from 'react-redux'
import Loader from '../../components/Spinner/Loader'

class Search extends Component {
  /**
   * Class constructor.
   */
  constructor (props, context) {
    super(props, context)

    // set the initial component state
    this.state = {
      errors: {},
      search: ''

    }

    this.onSubmit = this.onSubmit.bind(this)
    this.onChange = this.onChange.bind(this)
  }

  componentDidUpdate (prevProps) {
    if (this.props.search !== prevProps.search) {
      if (this.props.search.err) {
        this.setState({
          errors: this.props.search.err
        })
      }
    }
  }

  componentWillReceiveProps (nextProps) {
    /*  if (nextProps.session.user && nextProps.session.logged) {
        this.context.router.history.replace('/dashboard')
      } */
  }

  componentDidMount () {
    if (this.props.search.didInvalidate) {
      // TODO ADD the filter
    }
  }
  /**
   * Process the form.
   *
   * @param {object} event - the JavaScript event object
   */
  onSubmit (event) {
    // prevent default action. in this case, action is the form submission event
    event.preventDefault()
    if (this.state.search !== '') {
      // action
    }
  }
  /**
   * Change the user object.
   *
   * @param {object} event - the JavaScript event object
   */
  onChange (event) {
    return this.setState({search: event.target.value})
  }

  /**
   * Render the component.
   */
  render () {
    if (this.props.search.didInvalidate || this.props.search.isFetching) {
      return (<Loader />)
    }
    return (
      <SearchBar
        onSubmit={this.onSubmit}
        onChange={this.onChange}
        errors={this.state.errors}
        search={this.state.search} />

    )
  }
}

Search.propTypes = {
  search: PropTypes.object.isRequired
}
Search.contextTypes = {
  router: PropTypes.object.isRequired
}
function mapStateToProps (state) {
  console.log(state)
  const { search } = state
  return {
    search
  }
}
/*
function mapDispatchToProps (dispatch) {
  return {
    actions: bindActionCreators(actions, dispatch)
  }
} */
export default connect(mapStateToProps)(Search)



