import * as types from './actionTypes'
import * as profileTypes from '../../actionTypes'
export default function myEducationReducer (state = {}, action) {
  switch (action.type) {
    case types.ADD_EDUCATION_REQUEST:
      return {...state,
        isRequesting: true,
        didInvalidate: false
      }
    case profileTypes.GET_MY_PROFILE_SUCCESS:
      return {...state,
        items: action.content.educations,
        didInvalidate: false
      }
    case types.ADD_EDUCATION_FAILED:
      return {...state,
        isRequesting: false,
        err: action.err,
        message: action.message,
        didInvalidate: false
      }
    case types.ADD_EDUCATION_SUCCESS:
      return {...state,
        isRequesting: false,
        logged: false,
        message: action.message,
        content: action.content,
        didInvalidate: false
      }

    case types.CREATE_EDUCATION:
      return {...state,
        isEditing:true
      }

    default:
      return state
  }
}



