import React from 'react'
import PropTypes from 'prop-types'
import {List, ListItem} from 'material-ui/List'

import {green700, transparent} from 'material-ui/styles/colors'
import ActionAddPerson from 'material-ui/svg-icons/social/person-add'

const Users = ({users}) => (<List>
  {users.map(function (item) {
    return (
      <ListItem
        key={item._id}
        primaryText={item.familly_name + ' ' + item.first_name}
      />)
  })}
</List>)

Users.propTypes = {
  universities: PropTypes.array.isRequired
}
export default Users



