import React, { Component} from 'react'
import PropTypes from 'prop-types'
import cookie from 'react-cookies'
import {bindActionCreators} from 'redux'
import {connect} from 'react-redux'
import * as actions from './action'
import Loader from '../../../../../components/Spinner/Loader'
import SearchBar from '../../../components/SearchBar'
class UserSearch extends Component {
  /**
   * Class constructor.
   */
  constructor (props, context) {
    super(props, context)

    // set the initial component state
    this.state = {
      errors: {},
      search: ''

    }

    this.onSubmit = this.onSubmit.bind(this)
    this.onChange = this.onChange.bind(this)
  }

  componentDidUpdate (prevProps) {
    if (this.props.userSearch !== prevProps.userSearch) {
      if (this.props.userSearch.err) {
        this.setState({
          errors: this.props.userSearch.err
        })
      }
      if (this.props.userSearch.done) {
        this.context.router.history.replace('/search')
      }
    }
  }

  componentWillReceiveProps (nextProps) {
    if (this.props.userSearch.done && (nextProps.userSearch !== this.props.userSearch)) {
      this.context.router.history.replace('/search')
    }

    /*  if (nextProps.session.user && nextProps.session.logged) {
        this.context.router.history.replace('/dashboard')
      } */
  }

  componentDidMount () {
    if (this.props.userSearch.didInvalidate) {

    }
    if (this.props.userSearch.done) {
      // TODO ADD the URI for Search
      this.context.router.history.replace('/search')
    }
  }
  /**
   * Process the form.
   *
   * @param {object} event - the JavaScript event object
   */
  onSubmit (event) {
    // prevent default action. in this case, action is the form submission event
    event.preventDefault()

      // action
    this.props.actions.searchUser(this.state.search)
  }
  /**
   * Change the user object.
   *
   * @param {object} event - the JavaScript event object
   */
  onChange (event) {
    return this.setState({search: event.target.value})
  }

  /**
   * Render the component.
   */
  render () {
    if (this.props.userSearch.didInvalidate || this.props.userSearch.isFetching) {
      return (<Loader />)
    }
    return (
      <div />

    )
  }
}

/** *
 *
 *
 * @type {{userSearch: *}}
 */
UserSearch.propTypes = {
  userSearch: PropTypes.object.isRequired
}
UserSearch.contextTypes = {
  router: PropTypes.object.isRequired
}
function mapStateToProps (state) {
  console.log(state)
  const { userSearch } = state.search.publicContentSearch
  return {
    userSearch
  }
}

function mapDispatchToProps (dispatch) {
  return {
    actions: bindActionCreators(actions, dispatch)
  }
}
export default connect(mapStateToProps, mapDispatchToProps)(UserSearch)



