import React from 'react'

import FlatButton from 'material-ui/FlatButton'
import AccountIcon from 'material-ui/svg-icons/action/account-box'

const SignUpButton = () => (
  <FlatButton
    href='https://localhost:3000/signUp'
    label='Sign up'
    primary
    icon={<AccountIcon />}
  />
)
export default SignUpButton



