import request from 'superagent'
class Api {
  static search (text) {
    return request.post('https://localhost:3000/public/search/universities')
      .type('form')
      .send({text: text})
      .then((res, err) => {
        if (err) {
          console.log(err)
        }
        console.log(res.status)
        if (res) {
          return res.body
        }
      })
  }
}

export default Api



