import myContentSearch from './admin/reducer'
import OtherContentSearch from './private/reducer'
import publicContentSearch from './public/reducer'

import {combineReducers} from 'redux'

const search = combineReducers({
  myContentSearch,
  OtherContentSearch,
  publicContentSearch
})

export default search



