import * as types from './actionTypes'

export default function signUpReducer (state = {}, action) {
  switch (action.type) {
    case types.SIGNUP_FAILED:
      return {...state,
        isRequesting: false,
        err: action.err,
        hasSignedup: false,
        message: action.message
      }
    case types.SIGNUP_REQUEST:
      return {...state,
        isRequestin: true,
        logged: false,
        didInvalidate: false
      }

    case types.SIGNUP_SUCCESS:
      return {...state,
        isRequesting: false,
        hasSignedup: true,
        message: action.message
      }
    default:
      return state
  }
}



