
import request from 'superagent'
import Auth from '../../Auth/auth'

class Api {
  static getMyContent () {
    return request.get('https://localhost:3000/api/myprofile')
      .set({'Content-Type': 'application/json', 'Authorization': 'Bearer ' + Auth.getToken()})
      .then((res) => {
        return res.body
      })
      .catch(error => {
        return error
      })
  }
}
export default Api



