import React, { Component} from 'react'
import PropTypes from 'prop-types'
import SignUpForm from './components/SignUpForm.jsx'
import Loader from '../../../../../components/Spinner/Loader'
import cookie from 'react-cookies'
import {bindActionCreators} from 'redux'
import {connect} from 'react-redux'
import * as actions from './actions'
class SignUpPage extends Component {
  /**
   * Class constructor.
   */
  constructor (props, context) {
    super(props, context)

    // set the initial component state
    this.state = {
      errors: {},
      credentials: {
        email: '',
        firstname: '',
        famillyname: '',
        password: ''
      }
    }

    this.processForm = this.processForm.bind(this)
    this.changeUser = this.changeUser.bind(this)
  }

  componentDidUpdate (prevProps) {
    if (this.props.signUp !== prevProps.signUp) {
      if (this.props.signUp.err) {
        this.setState({
          errors: this.props.signUp.err
        })
      }
    }
  }

  componentWillReceiveProps (nextProps) {
  /*  if (nextProps.session.hasSignedup) {
      this.context.router.history.replace('/login')
    } */
  }

  componentDidMount () {

    /* if (this.props.signup.didInvalidate) {
      this.props.actions.refreshUserIfNeeded(this.props.session)
    } */
  }
  /**
   * Process the form.
   *
   * @param {object} event - the JavaScript event object
   */
  processForm (event) {
    // prevent default action. in this case, action is the form submission event
    event.preventDefault()
    this.props.actions.signupUser(this.state.credentials)
  }

  /**
   * Change the user object.
   *
   * @param {object} event - the JavaScript event object
   */
  changeUser (event) {
    const field = event.target.name
    const credentials = this.state.credentials
    credentials[field] = event.target.value
    return this.setState({credentials: credentials})
  }

  /**
   * Render the component.
   */
  render () {
    return (
      <SignUpForm
        onSubmit={this.processForm}
        onChange={this.changeUser}
        errors={this.state.errors}
        credentials={this.state.credentials}
      />
    )
  }
}

SignUpPage.contextTypes = {
  router: PropTypes.object.isRequired
}

SignUpPage.propTypes = {
  signUp: PropTypes.object.isRequired
}
function mapStateToProps (state) {
  console.log(state)
  const { signUp } = state.session.visitor

  return {
    signUp
  }
}
function mapDispatchToProps (dispatch) {
  return {
    actions: bindActionCreators(actions, dispatch)
  }
}
export default connect(mapStateToProps, mapDispatchToProps)(SignUpPage)



